class Assets {
  constructor() {
    this.loadSequence = ["loadModels", "loadSounds","loadVideos", "loadTextures"]

    this.assets = {
      models: {},
      sounds: {},
      textures: {},
      videos: {},
    }

    this.manager = new LoadingManager()

    this.loaders = {
      models: new GLTFLoader(this.manager),
      sounds: new AudioLoader(this.manager),
      textures: new TextureLoader(this.manager),
      // videos:  new VideoLoader(this.manager)
    
    }

    this.completedSteps = {
      download: false,
      audioBuffers: false,

      models: false,
    }
    this.scene = new Scene()
    this.mixer = null; 
    this.audioBufferCount = 0
    this.modelLoadCount = 0
    this.videoLoadCount=0
  
    const dracoLoader = new DRACOLoader()
    dracoLoader.setDecoderPath("https://www.gstatic.com/draco/v1/decoders/")
    this.loaders.models.setDRACOLoader(dracoLoader)

    // this.init()
  }


  checkComplete() {
    const complete = Object.keys(this.completedSteps).reduce((previous, current) =>
      !previous ? false : this.completedSteps[current]
    )
    if (complete) this.onLoadSuccess()
  }


  checkVideos() {
    if (this.videoLoadCount === T0_LOAD.videos.length) {
        this.completedSteps.videos = true;
        this.checkComplete();
    }
}   
checkBuffers() {
  if (this.audioBufferCount === T0_LOAD.sounds.length) {
    this.completedSteps.audioBuffers = true
    this.checkComplete()
  }
}
  checkModels() {
    if (this.modelLoadCount === T0_LOAD.models.length) {
      this.completedSteps.models = true
      this.checkComplete()
    }
  }

  load(onLoadSuccess, onLoadError) {
    this.onLoadSuccess = onLoadSuccess

    this.onLoadError = (err) => {
      console.error(err)
      onLoadError(err)
    }

    this.manager.onStart = (url, itemsLoaded, itemsTotal) => {
      console.log(`Started loading file: ${url} \nLoaded ${itemsLoaded} of ${itemsTotal} files.`)
    }

    this.manager.onLoad = () => {
      console.log("Loading complete! , mrehbaa bik alfunun f portfolio dyali")
      this.completedSteps.download = true
      this.checkComplete()
    }

    // this.manager.on

    
    this.manager.onProgress = (url, itemsLoaded, itemsTotal) => {
      document.body.style.setProperty("--loaded", itemsLoaded / itemsTotal)

    }

    this.manager.onError = (url) => {
  
      this.onLoadError(`error loading ${url}`)
    }

    this.loadNext()
  }

  loadNext() {
    if (this.loadSequence.length) {
      this[this.loadSequence.shift()]()
    } else {
    }
  }
  loadModels() {
    T0_LOAD.models.forEach((item) => {
        if (item.id === "Avatar") {
            let fbx;

            // Chargement du modle FBX principal
            const fbxLoader = new FBXLoader(this.manager);
            fbxLoader.load(
                item.file,
                (loadedFbx) => {
                    fbx = loadedFbx; // Affecter loadedFbx  fbx
                    if (item.position) fbx.position.set(...item.position);
                    if (item.scale) fbx.scale.set(item.scale, item.scale, item.scale);

                    // Configuration des ombres pour les objets du modle
                    fbx.traverse(c => {
                        c.castShadow = true;
                    });

                    // Enregistrement du modle dans les actifs
                    this.assets.models[item.id] = fbx;

                    // Chargement des animations du modle
                    const animations = fbx.animations;
                    
                    // Cration du mlangeur (AnimationMixer) et ajout des animations
                    this.mixer = new AnimationMixer(fbx);
                    // this.assets.mixers[item.id] = this.mixer; // Stockage du mlangeur
                    
                    animations.forEach((clip) => {
                        const action = this.mixer.clipAction(clip);
                        action.play(); // Lancement de l'animation
                    });
                    
                    this.scene.add(fbx); // Ajout du modle  la scne

                    // Incrmentation du compteur de chargement des modles
                    this.modelLoadCount++;
                    this.checkModels(); // Vrification de l'achvement du chargement des modles
                }
            );
        } else {
            this.loaders.models.load(item.file, (gltf) => {
     
                if (item.position) gltf.scene.position.set(...item.position);
                if (item.scale) gltf.scene.scale.set(item.scale, item.scale, item.scale);
                this.assets.models[item.id] = gltf;
                this.modelLoadCount++;


                this.checkModels(); // Vrification de l'achvement du chargement des modles
            });
        }
    });

    this.loadNext(); // Passage  l'tape suivante du chargement
}


  loadSounds() {
    T0_LOAD.sounds.forEach((item) => {
      this.assets.sounds[item.id] = null
      this.loaders.sounds.load(item.file, (buffer) => {
        this.assets.sounds[item.id] = buffer
  
        this.audioBufferCount++
        this.checkBuffers()
      })
    })
    this.loadNext()
  }

  loadTextures() {
    T0_LOAD.textures.forEach((item) => {
      this.loaders.textures.load(item.file, (texture) => {

        this.assets.textures[item.id] = texture
      })
    })

    this.loadNext()
  }
  loadVideos() {
    T0_LOAD.videos.forEach((item) => {
        this.assets.videos[item.id] = null;
        const video = document.createElement('video');
        video.src = item.file;
        video.load();
        video.addEventListener('loadeddata', () => {

            this.assets.videos[item.id] = video;

            this.videoLoadCount++;
            this.checkVideos();
        });
    });
    this.loadNext();
}

  getModel(id, deepClone) {

    const group = new Group()
    const scene = this.assets.models[id].scene.clone()

    scene.traverse((item) => {
      if (item.isMesh) {
        item.home = {
        
          position: item.position.clone(),
          rotation: item.rotation.clone(),
          scale: item.scale.clone(),
        }

        if (deepClone)  ; item.material = item.material.clone()
      }
    })

    group.add(scene)
    return { group, scene, animations: this.assets.models[id].animations }
  }


  getModel1(id, deepClone) {

    const group = new Group()
    const scene = this.scene

    const clonedMaterials = []; // Tableau pour stocker les matriaux clons
    const animations = this.assets.models[id].animations || [];

    scene.traverse((item) => {
        if (item.isMesh) {
            item.home = {
                position: item.position.clone(),
                rotation: item.rotation.clone(),
                scale: item.scale.clone(),
            }
            // Si deepClone est vrai, clonez le matriau et stockez-le dans le tableau clonedMaterials
            if (deepClone) {
                const clonedMaterial = item.material.clone();
                clonedMaterials.push(clonedMaterial);
                item.material = clonedMaterial;
            }
        }
    });

    group.add(scene);

    // Attendre que les animations du modle soient charges et le mixer initialis
    // this.loadModels();

    // Rcuprer le mixer aprs le chargement des animations
    const mixer = this.mixer;
  

    return { group, scene, animations, mixer, clonedMaterials }; // Retourne galement les matriaux clons
}

  getTexture(id) {
    return this.assets.textures[id]
  }

  getSound(id) {
    return this.assets.sounds[id]
  }
  getVideo(id) {
   
    return this.assets.videos[id]
  }
}

const ASSETS = new Assets()

export { ASSETS }
